/* eslint-disable react/no-array-index-key */
// import React, { useEffect, useState } from 'react'
import React, { useState } from 'react'
import Layout from '../../layouts/main'
import Box from '../ui/Box'
import Card from '../ui/Card'
import Flex from '../ui/Flex'
import Heading from '../ui/Heading'
import Text from '../ui/Text'
import BarLineChart from '../Charts'

// Mocking data form public/api
const mockdata = window.MOCK_DATA || null
const chartData = mockdata && mockdata.data
const chartColor = mockdata ? mockdata.settings.theme : 'grey'
const primaryChart = mockdata && chartData.find((item) => item.isPrimary)

const carouselHeight = (mockdata && mockdata.settings.carouselHeight) || 30
const primaryChartHeight = 100 - carouselHeight

const isCarousel = mockdata && mockdata.settings.carousel
const carouselPosition = mockdata && mockdata.settings.carouselPosition === 'bottom' ? 0 : -1

const getTitle = (data) => data.title && data.title.text
const getSubtitle = (data) => data.subTitle && data.subTitle.text
const getDiscription = (data) => data.description && data.description.text

const Home = () => {
  const [selectedChart, setSelectedChart] = useState(primaryChart)

  const handleClick = (dataId) =>
    setSelectedChart(mockdata && chartData.find((item) => item.id === dataId))

  return (
    <Layout>
      <Flex height="100vh" flexWrap="wrap" alignContent="baseline">
        <Box height={`${primaryChartHeight}vh`} width={1} p={{ _: 1, lg: 2 }}>
          <Card primary display="flex" flexWrap="wrap" border={1} borderColor="grey300">
            <Box width={1} height="25%">
              {getTitle(selectedChart) && (
                <Text fontSize={2} mb={2} color={selectedChart.title.color}>
                  {getTitle(selectedChart)}
                </Text>
              )}

              {getSubtitle(selectedChart) && (
                <Text
                  as="p"
                  fontWeight="bold"
                  fontSize={5}
                  mb={2}
                  color={selectedChart.subTitle.color}
                >
                  {getSubtitle(selectedChart)}
                </Text>
              )}

              {getDiscription(selectedChart) && (
                <Text as="p" mb={2} color={selectedChart.description.color}>
                  {getDiscription(selectedChart)}
                </Text>
              )}
            </Box>
            <Box width={1} height="75%">
              <BarLineChart data={selectedChart} themeColor={chartColor} />
            </Box>
          </Card>
        </Box>

        <Flex
          width={1}
          alignContent="baseline"
          height={`${carouselHeight}vh`}
          overflow="scroll"
          flexWrap={isCarousel ? null : 'wrap'}
          order={carouselPosition}
        >
          {mockdata &&
            chartData.map(
              (item, idx) =>
                item.id !== selectedChart.id && (
                  <Box
                    key={idx}
                    width={isCarousel ? 1 : { _: 1 / 2, md: 1 / 4 }}
                    height="100%"
                    p={{ _: 1, lg: 2 }}
                    onClick={() => handleClick(item.id)}
                  >
                    <Card border={1} borderColor="grey300">
                      <Box width={1} height="30%">
                        {getTitle(item) && (
                          <Text as="p" fontSize={0} color={item.title.color}>
                            {getTitle(item)}
                          </Text>
                        )}

                        {getSubtitle(item) && (
                          <Text fontWeight="bold" mb={1} color={item.subTitle.color}>
                            {getSubtitle(item)}
                          </Text>
                        )}

                        {getDiscription(item) && (
                          <Text as="p" mb={2} color={item.description.color}>
                            {item.description.text}
                          </Text>
                        )}
                      </Box>
                      <Box width={1} height="70%">
                        <BarLineChart data={item} themeColor={chartColor} />
                      </Box>
                    </Card>
                  </Box>
                )
            )}
        </Flex>
      </Flex>
    </Layout>
  )
}

export default Home
