export const addAliases = (arr, aliases) =>
  aliases.forEach((key, i) =>
    Object.defineProperty(arr, key, {
      enumerable: false,
      get() {
        return this[i]
      },
    })
  )

export const flattenColors = (palette) => {
  const colors = {}
  Object.entries(palette).forEach(([color, shades]) => {
    Object.entries(shades).forEach(([shade, value]) => {
      colors[`${color}${shade}`] = value
    })
  })
  return colors
}

export const getThemeName = () => {
  return Window.MOCK_DATA ? Window.MOCK_DATA.themeName : 'blue'
}

export const hexToRGBa = (h, opacity) => {
  let r = 0
  let g = 0
  let b = 0

  // 3 digits
  if (h.length === 4) {
    r = `0x${h[1]}${h[1]}`
    g = `0x${h[2]}${h[2]}`
    b = `0x${h[3]}${h[3]}`

    // 6 digits
  } else if (h.length === 7) {
    r = `0x${h[1]}${h[2]}`
    g = `0x${h[3]}${h[4]}`
    b = `0x${h[5]}${h[6]}`
  }

  return `rgba(${+r},${+g},${+b},0.${opacity})`
}
