/* eslint-disable react/jsx-props-no-spreading */
import React from 'react'
import PropTypes from 'prop-types'
import Box from '../Box'
// import { Wrapper } from './Card.css'

const Card = ({ badge, children, primary, ...props }) => (
  <Box p={2} height="100%" {...props}>
    {children}
  </Box>
)

Card.propTypes = {
  ...Box.propTypes,
  children: PropTypes.node.isRequired,
  /** Styles the card more prominently */
  primary: PropTypes.bool,
}

Card.defaultProps = {
  primary: false,
}
export default Card
