/* eslint-disable react/jsx-props-no-spreading */
import React from 'react'
import PropTypes from 'prop-types'
import propTypes from '@styled-system/prop-types'
import { Heading as StyledHeading } from './Heading.css'

const HEADING_ELEMENT = {
  1: 'h1',
  2: 'h2',
  3: 'h3',
  4: 'h4',
  5: 'h5',
}

const resolveElement = (level) => {
  if (!HEADING_ELEMENT[level]) {
    return HEADING_ELEMENT[1]
  }

  return HEADING_ELEMENT[level]
}

const Heading = ({ level, children, ...props }) => (
  <StyledHeading as={resolveElement(level)} level={level} {...props}>
    {children}
  </StyledHeading>
)

Heading.propTypes = {
  /** Hierarchical level of the heading */
  level: PropTypes.oneOf([1, 2, 3, 4, 5]),
  children: PropTypes.node,
  className: PropTypes.string,
  ...propTypes.flexbox,
  ...propTypes.space,
  ...propTypes.typography,
}

Heading.defaultProps = {
  level: 1,
  children: '',
  className: null,
}

export default Heading
