/* eslint-disable react/jsx-no-comment-textnodes */
/* eslint-disable react/jsx-props-no-spreading */
import React from 'react'
import PropTypes from 'prop-types'
import { Bar, Line } from 'react-chartjs-2'
import { useTheme } from 'styled-components'
import { hexToRGBa } from '../../lib/theme/themeHelpers'
import { getOptions } from './chartConfig'

// const colourWhitelist = ['blue', 'grey', 'magenta', 'green', 'orange']

const BarLineChart = ({ data, themeColor, options, ...props }) => {
  const themeOptions = { theme: useTheme(), chartColor: themeColor }

  data.datasets[0].backgroundColor = hexToRGBa(
    themeOptions.theme.colors[`${themeOptions.chartColor}800`],
    25
  )
  data.datasets[0].borderColor = themeOptions.theme.colors[`${themeOptions.chartColor}800`]

  if (data.type === 'bar') {
    return (
      <Bar data={data} type="bar" backgroundColor="red" options={getOptions(options)} {...props} />
    )
  }

  return <Line data={data} type="line" options={getOptions(options)} {...props} />
}

BarLineChart.propTypes = {
  // eslint-disable-next-line react/forbid-prop-types
  options: PropTypes.any,
  // eslint-disable-next-line react/forbid-prop-types
  data: PropTypes.any.isRequired,
  /** Styles the card more prominently */
  themeColor: PropTypes.string.isRequired,
}

BarLineChart.defaultProps = {
  options: null,
}

export default BarLineChart
