/* eslint-disable import/prefer-default-export */
import styled, { css } from 'styled-components'
import { flexbox, space, typography } from 'styled-system'
import { themeGet } from '@styled-system/theme-get'

const levelStyles = (level) => {
  switch (level) {
    case 1:
    default:
      return css`
        font-size: ${themeGet('fontSizes.6')}px;
      `
    case 2:
      return css`
        font-size: ${themeGet('fontSizes.5')}px;
      `
    case 3:
      return css`
        font-size: ${themeGet('fontSizes.4')}px;
      `
    case 4:
      return css`
        font-size: ${themeGet('fontSizes.3')}px;
      `
    case 5:
      return css`
        font-size: ${themeGet('fontSizes.2')}px;
      `
  }
}

export const Heading = styled.span`
  color: ${themeGet('colors.textHeading')};
  font-family: ${themeGet('fonts.condensed')};
  line-height: normal;
  ${({ level }) => levelStyles(level)}
  ${flexbox}
  ${space}
  ${typography}
`
